//Import Layout files
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-size: 18px;
  list-style-type: none;
  text-decoration: none;
}

.body-container {
  width: 100vw;
  height: auto;
  @include flexthing;
  flex-direction: column;
  text-align: top;
  align-items: top;
  position: relative;

  h1 {
    @include flexthing;
    width: 100vw;
    height: 20vh;
  }
  h2 {
    font-size: 25px;
  }
  //for the start loading part effect
  .backroundi {
    @include flexthing;
    top: 1vh;
    position: absolute;
    background-color: blue;
    width: 100vw;
    height: 90vh;
  }
  .stars-container {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100%;
    height: 3rem;
    width: 3rem;
    position: relative;
    background: linear-gradient(
      to left,
      rgba(255, 255, 255, 0.8),
      #003366,
      #000d1a,
      #000d1a,
      #000d1a,
      #000000c2
    );
    @include add-move-animation(20s);
  }
  @for $i from 1 through 18 {
    .star:nth-child(#{$i}) {
      @include add-move-animation(6s);
      @include sterne();

      width: 0.4rem;
      height: 0.4rem;

      left: 5 + 0.1 * ($i * $i) + rem;
      top: -2$i * 0.2 + rem;
    }
  }
  //about me , in general context
  .about-me {
    @include flexthing;
    flex-direction: column;
    justify-content: space-between;

    padding: 5%;
    width: 70vw;
    height: 150vh;

    .technologies {
      text-align: center;

      width: 90vw;
      height: 100vh;
      border: 1px #4aff1823 solid;
      border-radius: 5%;
    }
  }
}

//for the my work component link
.parent-div-for-technologies-and-menu {
  @include flexthing;
  flex-direction: column;
  flex-wrap: wrap;
}
nav {
  display: flex;
  justify-content: space-between;
  width: 80%;
  height: auto;
}
.mywork-container {
  width: 90vw;
  height: 80vh;
  border: 2px red dashed;
  color: white;
  @include flexthing;
}
//for get in touch container
.getintouch-container {
  @include flexthing;

  width: 80vw;
  height: 80vh;

  .links-container {
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    flex-direction: column;

    width: 30%;
    height: 100%;
    a {
      @include flexthing;
      @include forfonts;
      display: flex;
      justify-content: space-around;

      width: 100%;
      font-size: 2rem;
    }

    img {
      padding: 12px;
      background-color: rgb(0, 0, 0);
      box-shadow: 0px 0px 6px 1px rgb(135, 243, 150);
      border-radius: 100%;
      width: 90px;
      height: 90px;
    }

    .mail-icon {
      padding: 12px;
      border-radius: 100%;
      background-color: rgb(0, 0, 0);
      box-shadow: 0px 0px 6px 1px rgb(135, 243, 150);
      width: 70px;
      height: 70px;
    }
  }
  .form-container {
    @include flexthing;

    width: 70%;
    height: 100%;
  }
}
//footer layout
footer {
  @include flexthing;
  width: 100%;
  height: 3rem;
  background: linear-gradient(#000000, #399c3977);
}
//mobile
@media only screen and (max-width: 500px) {
  .getintouch-container {
    @include flexthing;
    flex-direction: column;
    width: 100%;
    height: auto;
    .links-container {
      @include flexthing;
      flex-direction: column;
      img,
      .mail-icon {
        padding: 0px;
        width: 30px;
        height: 30px;
      }
      width: 100%;
      height: auto;
      a {
        margin: 0.3rem;
        font-size: 1rem;
        justify-content: space-between;
      }
    }
    .form-container {
      @include flexthing;
      flex-direction: row;
      width: 100%;
      height: auto;
    }
  }
  img {
    padding: 0px;
    width: 30px;
    height: 30px;
  }
  h1 {
    text-align: center;
  }
  nav {
    width: 100%;
    height: auto;
    li {
      font-size: 15px;
      padding: 1.2rem;
    }
  }
}
